import { Component, OnInit } from '@angular/core';
import { DesignUxNewsServiceComponent } from './shared/design-ux-news-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'news-app-design-ux-list',
  template: `
    <div *ngFor="let designuxnews of designuxnewss">
      <news-design-ux-news-list (click)="handeThumbnailClick(designuxnews.name)" [designuxnews]="designuxnews"></news-design-ux-news-list>
    </div>
  `,
})
export class NewsDesignUxListComponent implements OnInit {
  designuxnewss: any[]

  constructor(private DesignUxNewsServiceComponent: DesignUxNewsServiceComponent, private toastr:ToastrService) {
   }

  ngOnInit() {
    this.designuxnewss =  this.DesignUxNewsServiceComponent.getDesignUxNewss();
  }
}
