import { Component, OnInit } from '@angular/core';
import { DesignWebPhotoServiceComponent } from './shared/design-web-photo-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'photo-app-design-web-list',
  template: `
    <div *ngFor="let designwebphoto of designwebphotos">
      <design-web-photo-list (click)="handeDesignWebClick(designwebphoto.name)" [designwebphoto]="designwebphoto"></design-web-photo-list>
    </div>
  `,
})
export class PhotoDesignWebListComponent implements OnInit {
  designwebphotos: any[]
  
  constructor(private DesignWebPhotoServiceComponent: DesignWebPhotoServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designwebphotos =  this.DesignWebPhotoServiceComponent.getDesignWebPhotos()
  }
}
