import { Component, OnInit } from '@angular/core';
import { DesignUxPhotoServiceComponent } from './shared/design-ux-photo-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'photo-app-design-ux-list',
  template: `
    <div *ngFor="let designuxphoto of designuxphotos">
      <photo-design-ux-photo-list (click)="handeUxThumbnailClick(designuxphoto.name)" [designuxphoto]="designuxphoto"></photo-design-ux-photo-list>
    </div>
  `,
})
export class PhotoDesignUxListComponent implements OnInit {
  designuxphotos: any[]
  
  constructor(private DesignUxPhotoServiceComponent: DesignUxPhotoServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designuxphotos =  this.DesignUxPhotoServiceComponent.getDesignUxPhotos();
  }
}
