import { Component, OnInit } from '@angular/core';
import { DesignGeneralPhotoServiceComponent } from './shared/design-general-photo-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'photo-app-design-general-list',
  template: `
    <div *ngFor="let designgeneralphoto of designgeneralphotos">
      <design-general-photo-list (click)="handeDesignGeneralClick(designgeneralphoto.name)" [designgeneralphoto]="designgeneralphoto"></design-general-photo-list>
    </div>
  `,
})
export class PhotoDesignGeneralListComponent implements OnInit {
  designgeneralphotos: any[]

  constructor(private DesignGeneralPhotoServiceComponent: DesignGeneralPhotoServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designgeneralphotos =  this.DesignGeneralPhotoServiceComponent.getDesignGeneralPhotos()
  }
}
