//Y LE DAMOS LA FUNCION EL CLICK ROUTER

import { Component, OnInit } from '@angular/core';
import { DesignUxPostServiceComponent } from './shared/design-ux-post-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'post-app-design-ux-list',
  template: `
    <div *ngFor="let designuxpost of designuxposts">
      <design-ux-post-list (click)="handeThumbnailClick(designuxpost.name)" [designuxpost]="designuxpost"></design-ux-post-list>
    </div>
  `,
})
export class PostDesignUxListComponent implements OnInit {
  designuxposts: any[]
  
  constructor(private DesignUxPostServiceComponent: DesignUxPostServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designuxposts =  this.DesignUxPostServiceComponent.getDesignUxPosts();
  }
}
