import { Component, OnInit } from '@angular/core';
import { DesignOthersPostServiceComponent } from './shared/design-others-post-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'app-design-others-list',
  template: `
    <div *ngFor="let designotherspost of designothersposts">
      <design-others-post-list (click)="handeDesignOthersClick(designotherspost.name)" [designotherspost]="designotherspost"></design-others-post-list>
    </div>
  `,
})
export class DesignOthersListComponent implements OnInit {
  designothersposts: any[]
  
  constructor(private DesignOthersPostServiceComponent: DesignOthersPostServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designothersposts =  this.DesignOthersPostServiceComponent.getDesignOthersPosts()
  }
}
