import { Component, OnInit, Input, Output } from '@angular/core';
import { DesignUxPhotoServiceComponent } from './categories/shared/design-ux-photo-service.component';
import { DesignOthersPhotoServiceComponent } from './categories/shared/design-others-photo-service.component';
import { DesignWebPhotoServiceComponent } from './categories/shared/design-web-photo-service.component';
import { DesignGeneralPhotoServiceComponent } from './categories/shared/design-general-photo-service.component';
import { ToastrService } from './common/services/toastr.service';

declare var $:any;//Declarando $ de Jquery
declare var jQuery:any;//Declarando Jquery

declare let toastr
@Component({
  selector: 'app-view-photo',
  templateUrl: './view-photo.component.html',
  styleUrls: ['./view-photo.component.css']
})
export class ViewPhotoComponent implements OnInit {
  //Declarando funciones de nuestros items
  designuxphotos: any[]
  designothersphotos: any[]
  designwebphotos: any[]
  designgeneralphotos: any[]

  constructor(
    //Metodos de los items photos en este componente
    private DesignUxPhotoServiceComponent: DesignUxPhotoServiceComponent, 
    private DesignOthersPhotoServiceComponent: DesignOthersPhotoServiceComponent,
    private DesignWebPhotoServiceComponent: DesignWebPhotoServiceComponent,
    private DesignGeneralPhotoServiceComponent: DesignGeneralPhotoServiceComponent,
    private toastr:ToastrService) {
   }

  ngOnInit() {
    //Ejecutar con el router la muestra de los items Photos
    this.designuxphotos =  this.DesignUxPhotoServiceComponent.getDesignUxPhotos();
    this.designothersphotos =  this.DesignOthersPhotoServiceComponent.getDesignOthersPhotos();
    this.designwebphotos =  this.DesignWebPhotoServiceComponent.getDesignWebPhotos();
    this.designgeneralphotos =  this.DesignGeneralPhotoServiceComponent.getDesignGeneralPhotos();

    //Llamando el método para el order random de los items
    this.randomOrder();
  }

  //Funcionalidad del metodo del order random para los items
  private randomOrder() {
    $(document).ready(function () {
      var aae_aleatory_items = document.querySelector('#aae_aleatory_items');
      for (var Aleatories = aae_aleatory_items.children.length; Aleatories >= 0; Aleatories--) {
        aae_aleatory_items.appendChild(aae_aleatory_items.children[Math.random() * Aleatories | 0]);
      }
    });
  }  
}
