import { Component, OnInit } from '@angular/core';
import { VideoServiceComponent } from './shared/video-service.component';
import { ActivatedRoute } from '@angular/router';

// Declaro las variables de jQuery
declare var jQuery:any;
declare var $:any;

@Component({
  selector: 'app-video-detail',
  templateUrl: './videos-detail.component.html',
  styleUrls: ['./videos-detail.component.css']
})
export class VideosDetailComponent implements OnInit {
  //Validando id de los videos
  video:any
  constructor(private VideoServiceComponent: VideoServiceComponent, private route:ActivatedRoute) { }
  
  ngOnInit() {
    this.video = this.VideoServiceComponent.getVideo
    (+this.route.snapshot.params['id'])
    //Pausa y play del video
    this.pausePlayVideo();
  }

  //Función para la pausa y el play del video Details Component
  private pausePlayVideo() {
    $(document).ready(function () {
      //Video detail Component
      $("#myVideo").click(function() {
        if (this.paused == false) {
            this.pause();
            //Aditional function pause
            alert('music paused');
        } else {
            this.play();
            //Aditional function play
            alert('music playing');
        }
      });
    });
  }
  
  //Función para esconder la descriptción con el *ngif
  show = false;
  buttonName = 'SEE MORE';
  hide: any;
  
  toggle() {
    this.show = !this.show

  if(this.show) {
    this.buttonName = 'SEE LESS'
    console.log(this.show)
  }
  else {
    this.buttonName = 'SEE MORE'
    }
  }

}
