import { Component, OnInit, Input, Output } from '@angular/core';
import { DesignUxPostServiceComponent } from './categories/shared/design-ux-post-service.component';
import { DesignWebPostServiceComponent } from './categories/shared/design-web-post-service.component';
import { DesignGeneralPostServiceComponent } from './categories/shared/design-general-post-service.component';
import { DesignOthersPostServiceComponent } from './categories/shared/design-others-post-service.component';
import { ToastrService } from './common/services/toastr.service';

declare var $:any;//Declarando $ de Jquery
declare var jQuery:any;//Declarando Jquery

declare let toastr
@Component({
  selector: 'app-view-post',
  templateUrl: './view-post.component.html',
  styleUrls: ['./view-post.component.css']
})
export class ViewPostComponent implements OnInit {
  //Declarando funciones de nuestros items
  designuxposts: any[]
  designwebposts: any[]
  designgeneralposts: any[]
  designothersposts: any[]
  
  constructor(
    //Metodos de los items Post en este componente
    private DesignUxPostServiceComponent: DesignUxPostServiceComponent,
    private DesignWebPostServiceComponent: DesignWebPostServiceComponent,
    private DesignGeneralPostServiceComponent: DesignGeneralPostServiceComponent,
    private DesignOthersPostServiceComponent: DesignOthersPostServiceComponent,
    private toastr:ToastrService) {
   }

  ngOnInit() {
    //Ejecutar con el router la muestra de los items Post
    this.designuxposts =  this.DesignUxPostServiceComponent.getDesignUxPosts();
    this.designwebposts =  this.DesignWebPostServiceComponent.getDesignWebPosts();
    this.designgeneralposts =  this.DesignGeneralPostServiceComponent.getDesignGeneralPosts();
    this.designothersposts =  this.DesignOthersPostServiceComponent.getDesignOthersPosts();

    //Llamando el método para el order random de los items
    this.randomOrder();
  }

  //Funcionalidad del metodo del order random para los items
  private randomOrder() {
    $(document).ready(function () {
      var aae_aleatory_items = document.querySelector('#aae_aleatory_items');
      for (var Aleatories = aae_aleatory_items.children.length; Aleatories >= 0; Aleatories--) {
        aae_aleatory_items.appendChild(aae_aleatory_items.children[Math.random() * Aleatories | 0]);
      }
    });
  }
}
