import { Router, ActivatedRouteSnapshot, CanActivate } from '@angular/router';
import { Injectable} from '@angular/core';
import { DesignGeneralPhotoServiceComponent } from './shared/design-general-photo-service.component';

@Injectable()
export class DesignGeneralPhotoRouteActivator implements CanActivate { //Redireccionamiento de los items en caso de no existir

    constructor (private DesignGeneralPhotoServiceComponent:DesignGeneralPhotoServiceComponent, private router:Router ){
    //Codigo que agregué para que la ruta id por parametros se recarge, es decir
    // Funciona tambien en caso de tener una misma lista de elementos almacenadas dentro de el id de un elemento, esto recargara, ejemplo: youtube,(entras a uno de sus videos y ves más videos recomendados, al darle a otro, este recarga la ruta)
    //Esto resuelve el error de que solo cargaba la ruta desde cada elemento y no manualmente.
        this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    }
    
    canActivate (route:ActivatedRouteSnapshot){
      const designgeneralphotoExists = !!this.DesignGeneralPhotoServiceComponent.getDesignGeneralPhoto(+route.params['id'])

      if(!designgeneralphotoExists)
        this.router.navigate(['./common/errorr'])//Redirecciona al error 404 original
        return designgeneralphotoExists
    }
}