import { Component, OnInit } from '@angular/core';
import { DesignGeneralPostServiceComponent } from './shared/design-general-post-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'app-design-general-list',
  template: `
    <div *ngFor="let designgeneralpost of designgeneralposts">
      <design-general-post-list (click)="handeDesignGeneralClick(designgeneralpost.name)" [designgeneralpost]="designgeneralpost"></design-general-post-list>
    </div>
  `,
})
export class DesignGeneralListComponent implements OnInit {
  designgeneralposts: any[]
  
  constructor(private DesignGeneralPostServiceComponent: DesignGeneralPostServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designgeneralposts =  this.DesignGeneralPostServiceComponent.getDesignGeneralPosts()
  }
}
