import { Component, OnInit } from '@angular/core';
import { VideoServiceComponent } from './shared/video-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html'
})
export class InicioComponent implements OnInit {
  videos: any[]
  
  constructor(private VideoServiceComponent: VideoServiceComponent, private toastr:ToastrService) {
   }

  ngOnInit() {
    this.videos =  this.VideoServiceComponent.getVideos();
  }

  handeThumbnailClick(videoName)  {
    this.toastr.success(videoName)
  }
}
