import { NewsDesignUxListComponent } from './modules/news/categories/ux-design-items/news-design-ux-list.component'
import { DesignUxNewssDetailComponent } from './modules/news/categories/ux-design-items/design-ux-newss-detail/design-ux-newss-detail.component';
