import { Component, OnInit } from '@angular/core';
import { DesignWebPostServiceComponent } from './shared/design-web-post-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'app-design-web-list',
  template: `
    <div *ngFor="let designwebpost of designwebposts">
      <design-web-post-list (click)="handeDesignWebClick(designwebpost.name)" [designwebpost]="designwebpost"></design-web-post-list>
    </div>
  `,
})
export class DesignWebListComponent implements OnInit {
  designwebposts: any[]
  
  constructor(private DesignWebPostServiceComponent: DesignWebPostServiceComponent, private toastr:ToastrService) {
  }

  ngOnInit() {
    this.designwebposts =  this.DesignWebPostServiceComponent.getDesignWebPosts()
  }
}
