import { Component, OnInit, Input, Output } from '@angular/core';
import { DesignUxNewsServiceComponent } from './categories/shared/design-ux-news-service.component';
import { DesignWebNewsServiceComponent } from './categories/shared/design-web-news-service.component';
import { DesignGeneralNewsServiceComponent } from './categories/shared/design-general-news-service.component';
import { DesignOthersNewsServiceComponent } from './categories/shared/design-others-news-service.component';
import { ToastrService } from './common/services/toastr.service';

declare var $:any;//Declarando $ de Jquery
declare var jQuery:any;//Declarando Jquery

declare let toastr
@Component({
  selector: 'app-view-news',
  templateUrl: './view-news.component.html',
  styleUrls: ['./view-news.component.css']
})
export class ViewNewsComponent implements OnInit {
  //Declarando funciones de nuestros items
  designuxnewss: any[]
  designwebnewss: any[]
  designgeneralnewss: any[]
  designothersnewss: any[]
  
  constructor(
    //Metodos de los items News en este componente
    private DesignUxNewsServiceComponent: DesignUxNewsServiceComponent, 
    private DesignWebNewsServiceComponent: DesignWebNewsServiceComponent,
    private DesignGeneralNewsServiceComponent: DesignGeneralNewsServiceComponent,
    private DesignOthersNewsServiceComponent: DesignOthersNewsServiceComponent,
    private toastr:ToastrService) {
   }

  ngOnInit() {
    //Ejecutar con el router la muestra de los items News
    this.designuxnewss =  this.DesignUxNewsServiceComponent.getDesignUxNewss();
    this.designwebnewss =  this.DesignWebNewsServiceComponent.getDesignWebNewss();
    this.designgeneralnewss =  this.DesignGeneralNewsServiceComponent.getDesignGeneralNewss();
    this.designothersnewss =  this.DesignOthersNewsServiceComponent.getDesignOthersNewss();

    //Llamando el método para el order random de los items
    this.randomOrder();
  }

  //Funcionalidad del metodo del order random para los items
  private randomOrder() {
    $(document).ready(function () {
      var aae_aleatory_items = document.querySelector('#aae_aleatory_items');
      for (var Aleatories = aae_aleatory_items.children.length; Aleatories >= 0; Aleatories--) {
        aae_aleatory_items.appendChild(aae_aleatory_items.children[Math.random() * Aleatories | 0]);
      }
    });
  }
}
